html,
body,
img {
  margin: 0;
  padding: 0;
}
// html,body{
//   background-color: #F2F2F2;
// }
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
a:link{
  text-decoration:none;
}
a:visited{
  text-decoration:none;
}a:hover{
   text-decoration:none;
  //  color: #C81623;
}
a:active{
  text-decoration:none;
}
#header{
  width: 100%;
  height: 70px;
  background-color: #ff6a00;
  color: #fff;
  position: relative;
  img{
    margin-left: 30px;
    float: left;
    vertical-align: center;
  }
  ul{
    float: left;
    list-style: none;
    margin: 0;
    margin-left: 30px;
    height: 100%;
    text-align: center;
    line-height: 70px;
    li{
      float: left;
      width: 108px;
    }
  }
  .right{
    float: right;
    height: 100%;
    width: 80px;
    text-align: center;
    line-height: 70px;
    a{
      color: #fff;
      padding-right: 10px;
    }
  }
}
.conent {
  .video{
  position: relative;
  .show{
    position: absolute;
    // width: 100%;
    left:50%;
    top:50%;
    transform: translate(-50%,-50%);
    text-align: center;
    color: #fff;
    font-size: 30px;
    font-weight: normal;
    h1,h2{
      color: #fff
    }
    .search{
      width: 700px;
      border-radius: 5px;
      background-color: hsla(0,0%,100%,.1);
      position:relative;
      border: 2px solid #fd6606;
      font-size: 14px;
      padding: 9px 0;
      input{
        width: 100%;
        background-color: hsla(0,0%,100%,0);
        color: #fff;
        padding-right: 60px;
        outline: none;
        padding: 5px 12px;
        font-size: 14px;
        border: none;
        line-height: normal;
        // font: inherit;
        margin: 0;
        -webkit-appearance: none;
        -webkit-tap-highlight-color: rgba(0,0,0,0);
    
      }
      .icon {
        position: absolute;
        right:18px;
        font-size: 24px;
        color: #fd6606;
        top:12px;
      }
    }

  }
  }
}

.bottom {
  // height: 500px;
  width: 100%;
  padding-top:40px;
  margin-bottom: 50px;
  overflow: hidden;
  .left{
    width: 65%;
    background-color: #fff;
    float: left;
    margin-left: 70px;
    margin-right: 30px;   
    -moz-box-shadow:2px 2px 10px #928f8f;;
     -webkit-box-shadow:2px 2px 10px #928f8f;;
      box-shadow:2px 2px 10px #928f8f;;
  }
  .right{
    height: 450px;
    width: 25%;
    float: left;
  }
  .top{
    width: 100%;
    height: 40%;
    margin-bottom: 19%;
    color: #fd6606;
    text-align: center;
    line-height: 170px;
    font-size: 35px;
    -moz-box-shadow:2px 2px 10px #928f8f;;
    -webkit-box-shadow:2px 2px 10px #928f8f;
     box-shadow:2px 2px 10px #928f8f;;
  }
  .bt{
    width: 100%;
    height: 40%;
    color: #000;
    text-align: center;
    line-height: 170px;
    font-size: 24px;
    -moz-box-shadow:2px 2px 10px #928f8f;;
    -webkit-box-shadow:2px 2px 10px #928f8f;
     box-shadow:2px 2px 10px #928f8f;;
  }
}

.job {
  .title{
    width: 100%;
    height: 50px;
    border-bottom: 1px solid rgb(245, 239, 239);
    line-height: 50px;
    padding:0 20px;
    .tit_job{ 
      float: left;
    }
    a{
      color: #ccc;
      display: block;
      float:right;
    }
  }
  ul{
    padding:0 20px;
    li{
      line-height: 74px;
      .company{
        display: block;
        float: left;
        width: 60%
      }
      .info{
        float:right;
        width:40%;
        .address{
          float: left;
        }
        .time{
          float: right;
        }
      }
    }
  }
 
}
